
import React from "react";
import { MathJaxContext, MathJax } from "better-react-mathjax";

const mathjaxConfig = {
  loader: { load: ['[tex]/ams'] },
  tex: {
    packages: { '[+]': ['ams'] },
    inlineMath: [['$', '$'], ['\\(', '\\)']],
    displayMath: [['$$', '$$'], ['\\[', '\\]']],
  },
};

const LatexRenderer = () => {
  return (
    <MathJaxContext config={mathjaxConfig}>
      <MathJax dynamic>
        <div>
          {/* Works with matrix */}
          {String.raw`\[
            \begin{matrix}
              \left( h_{1} - h_{2} \right)\rho gA = Ad\rho a \\
              \Rightarrow \left( h_{1} - h_{2} \right) = \frac{\text{Ad}\rho a}{\rho gA} = \frac{ad}{g}
            \end{matrix}
          \]`}

          <br />

          {/* Better formatting with align* */}
          {String.raw`\[
            \begin{align*}
              \left( h_{1} - h_{2} \right)\rho gA &= Ad\rho a \\
              \Rightarrow \left( h_{1} - h_{2} \right) &= \frac{\text{Ad}\rho a}{\rho gA} = \frac{ad}{g}
            \end{align*}
          \]`}
        </div>
      </MathJax>
    </MathJaxContext>
  );
};

export default LatexRenderer;
